html {
    font-size: 62.5%;
}

body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-size: 1rem;
    background-color: yellow;
}

@media (max-width: 375px) {
    html {
        font-size: 31.25%;
    }
}
@media (min-width: 376px) and (max-width: 575px) {
    html {
        font-size: 43.75%;
    }
}
